<template>
  <div>
    <div>
      <v-apisearch v-on:searchApi="reGetRecord"></v-apisearch>
    </div>

    <div>
      <!-- <el-scrollbar style='height:100%;overflow-x: hidden'> -->
      <el-table :data="recordlist" :empty-text="$t('common.nodata')" border>
        <el-table-column :label="$t('common.No')" type="index" width="50">
        </el-table-column>
        <el-table-column prop="in_time" width="200" :formatter="timeFormatter" :label="$t('apirecord.time')">
        </el-table-column>
        <el-table-column prop="interface_name" :label="$t('apirecord.interface_name')">
        </el-table-column>
        <el-table-column prop="res_data" :label="$t('apirecord.res_data')">
        </el-table-column>
      </el-table>
      <!-- </el-scrollbar> -->
    </div>
  </div>
</template>
<script>
  import {
    ApiRecords
  } from '@/request/api/material'; // 导入api接口
  import {
    timeConvert
  } from '@/utils/public'
  import VApisearch from './apisearch'
  export default {
    components: {
      VApisearch
    },
    data() {
      return {
        recordlist: [],
        params: {
          interface_type: '',
          start_time: '',
          end_time: ''
        }
      }
    },
    mounted() {
      this.getRecord()
    },
    methods: {
      getRecord() {
        ApiRecords(this.params).then(res => {
          if (res.result) {
            this.recordlist = res.data
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      },
      reGetRecord(value1, value2, value3) {
        this.params.interface_type = value1
        this.params.start_time = value2
        this.params.end_time = value3
        this.getRecord()
      },
      timeFormatter(value) {
        return timeConvert(value.in_time, 1)
      }
    }
  }
</script>



