<template>
  <div class="apirecord_header">
    <div class="operation_select">
      <el-select v-model="interface_type" @change="changeOPtions">
        <el-option v-for="item in options" :key="item.interface_type" :label="item.label" :value="item.interface_type">
        </el-option>
      </el-select>
    </div>
    <div class="operation_picker">
      <el-date-picker v-model="date" value-format="yyyy-MM-dd HH:mm:ss" type="daterange" :range-separator="$t('user.to')"
        :start-placeholder="$t('user.startdate')" :end-placeholder="$t('user.enddate')">
      </el-date-picker>
    </div>
    <div class="operation_btn">
      <el-button size="small" type="primary" @click="search">{{$t('common.search')}}</el-button>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        interface_type: '',
        options: [{
            'interface_type': '',
            'label': '所有'
          },
          {
            'interface_type': 0,
            'label': '上料接口'
          },
          {
            'interface_type': 1,
            'label': '上料成功回传接口'
          },
          {
            'interface_type': 2,
            'label': '取料偷料回传接口'
          },
          {
            'interface_type': 3,
            'label': '站位表数据接口'
          },
          {
            'interface_type': 4,
            'label': '站位表发料回传接口'
          },
        ],
        date: [],
        start_date: '',
        end_date: '',
      }
    },
    methods: {
      search() {
        if (this.date.length) {
          this.start_date = this.date[0]
          this.end_date = this.date[1]
        } else {
          this.start_date = '',
            this.start_date = ''
        }
        this.$emit('searchApi', this.interface_type, this.start_date, this.end_date)
      },
      changeOPtions() {

      }
    }
  }
</script>
<style>
  .apirecord_header {
    display: flex;
    height: 40px;
    line-height: 40px;
    margin: 10px;
  }

  .operation_picker {
    margin-left: 20px;
  }

  .operation_btn .el-button {
    height: 38px;
    margin-left: 20px;
  }
</style>



